import React, { PureComponent } from 'react'
import {Link } from 'react-router-dom'
export default class Answer extends PureComponent {
  state = {
    upvotes:0,
    downvotes:0,
    upvoted:0
  }
  upvote = ()=>{
      if(this.state.upvoted==0 || this.state.upvoted==-1 ){
        if(this.state.upvotes>=0 && this.state.downvotes==0){
          this.setState((prev)=>{
            return {
              upvotes:prev.upvotes+1,
              downvotes:prev.downvotes,
              upvoted:prev.upvoted+1
            }
          })
        }
        else{
          this.setState((prev)=>{
            return {
              upvotes:prev.upvotes,
              downvotes:prev.downvotes+1,
              upvoted:prev.upvoted+1
            }
          })
        }

      }
  }
  downvote = ()=>{
        
            if(this.state.upvoted==0 || this.state.upvoted==1 ){
                if(this.state.upvotes>0){
                  this.setState((prev)=>{
                    return {
                    upvotes:prev.upvotes-1,
                    downvotes:prev.downvotes,
                    upvoted:prev.upvoted-1
                    }
                  })
                }
                else{
                  this.setState((prev)=>{
                    return {
                    upvotes:prev.upvotes,
                    downvotes:prev.downvotes-1,
                    upvoted:prev.upvoted-1
                    }
                  })
                }
            }
  }

  render() {
      
         let {answered_by,answer,accepted,upvotes,downvotes } = this.props.data;
        //  console.log(this.props.data)
         upvotes = this.state.upvotes;
         downvotes = this.state.downvotes;
         let content = (<div className="font-post source_code card p-1 m-3">
             <div className="row post_img">
             <div className="col-2 mx-2">
                  <div className="row p-2">
                        <button className="btn btn-sm emoji" name="upvotes" onClick={this.upvote}>{upvotes}<span className="emoji">üëç</span> </button>
                  </div>
                  <div className="row p-2">
                        {accepted===true?<span className="accepted emoji" title="accepted by user">‚úÖ</span>:null}
                  </div>
                  <div className="row p-2">
                        <button className="btn btn-sm emoji" name="downvotes" onClick={this.downvote}>{downvotes}<span className="emoji">üëé</span>  </button>
                  </div>
               </div>
               <div className="col-9 p-2">
                  <div dangerouslySetInnerHTML={{__html:answer}}  className="paragraph"/>
                  <span className="text-right other_info"><Link to={"/profile/"+answered_by} className="nav-link" >-: {answered_by}</Link></span>
                  {/* <p className="time">{this.props.data.time}</p> */}
               </div>
             </div>
         </div>
         )
    return (
      <div>
        {content}
      </div>
    )
  }
}
