import React, { PureComponent } from 'react'
import Navbar from './Navbar';
import Topics from './Topics'

export default class Ask extends PureComponent {
    state = {
        question:''
    }
    formHandler = (event)=>{
        event.preventDefault();
        console.log(this.state.question)
    }
    inputHandler = (event)=>{
        let {name,value} = event.target;
            this.setState({[name]:value});
    }
  render() {
    return (
      <div>
        <Navbar />
         <div className="container m-3">
            <div className="row">
                <div className="col-3">
                <Topics />
                </div>
                <div className="col-8">
                    <form onSubmit={this.formHandler} method="POST" >
                        <input type="text" name="question" className="form-control source_code title m-2" placeholder="What is Your Question ? " onChange={this.inputHandler}/>
                        <button className="btn btn-primary m-2">submit</button>
                    </form>
                </div>
            </div>
         </div>
      </div>
    )
  }
}
