import React, { PureComponent } from 'react'
import {Link} from 'react-router-dom'
import logo from '../logo.svg'

export default class Navbar extends PureComponent {
    state = {
        search:''
    }

    searchHandler = (event)=>{
         event.preventDefault();
         if(this.props.search){
            this.props.searchMethod(this.state.search)
         }
    }
    inputHandler = (event)=>{
        let {name,value} = event.target;
        this.setState({[name]:value});
    }

  render() {
    return (
        
                <nav className="navbar navbar-expand-lg navbar-light shadow-sm">
                    <Link to="/home" className="nav-link brand website nav-item"> Quora </Link>  
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon">
                            </span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarText">
                            <ul className="navbar-nav font mr-auto m_l">
                                <li className="nav-item">
                                    <Link to="/home" className="nav-link" >Home </Link> <span className="sr-only">(current)</span> 
                                </li>
                                <li className="nav-item">
                                     <Link to="/answers" className="nav-link" >Answers</Link>                                                 
                                </li>
                                <li className="nav-item">
                                        <Link to="/notifications" className="nav-link" >Notifications</Link>                
                                </li>
                                <form className="form-inline my-2 my-lg-0 search nav-item " method="post" onSubmit={this.searchHandler}>
                                     <input className="form-control mr-sm-2" name="search" type="search" placeholder="Search" aria-label="Search" onChange={this.inputHandler} />
                                </form>
                                <li className="nav-item">
                                        <Link to="/ask" className="nav-link" >Ask</Link>                
                               </li>
                               <li className="nav-item">
                                        <Link to="/profile/user1" className="nav-link" ><img src={logo} className="img-circle nav_img" alt="img"/></Link>                
                               </li>
                            </ul>
                        </div>
                </nav>
                
            
    )
  }
}
