import React, { PureComponent } from 'react'
import database from '../data'
import loading from '../images/loading.gif'
import Question from './Question';
import Navbar from './Navbar';
import Topics from './Topics';


export default class Questions extends PureComponent {
    constructor(){
        super();
        this.state = {
            data:[],
            isLoading:true
        }
    }
    componentDidMount(){
        setTimeout(()=>{
            this.setState({data:database.questions,isLoading:false})
        },700);
    }
    searchAnswers = (keywords)=>{
      if(!this.state.isLoading){
        keywords = keywords.toLowerCase();
        let data = this.state.data.filter((question)=>{
                return question.title.toLowerCase().indexOf(keywords)>-1;
        })
        this.setState({data:data})
      }
      console.log(keywords)
    }
  render() {
    let content = <img src={loading} alt="loading" className="img-responsive" />;
    if(!this.state.isLoading){
        content = this.state.data.map((question)=>{
            return <Question data={question} key={question.id}/>;
        })
    }
    return (
      <div>
         <Navbar search={true} searchMethod={this.searchAnswers} />
            <div className="container container-m-5">
              <div className="row">
                  <div className="col-3">
                    <Topics />
                  </div>
                  <div className="col-7">
                     {content}
                  </div>
              </div>
            </div>
      </div>
    )
  }
}
