import React, { PureComponent } from 'react'
import {Link } from 'react-router-dom'
export default class Post extends PureComponent {

  render() {
      
         let user = this.props.data.username;
         let content = (<div className="post card">
             <div className="question_title">{this.props.data.title}</div>
             <div className="row">
               <div className="col-12">
               <Link to={"/profile/"+user} className="nav-link" ><img src={this.props.data.profile_img} className="img-circle profile_img" alt="img"/></Link>
               <p><Link to={"/profile/"+user} className="nav-link" >{this.props.data.name}</Link></p>
               {/* <p className="time">{this.props.data.time}</p> */}
               </div>
               <div className="col-12">
                   {this.props.data.answer}
               </div>
             </div>
         </div>
         )
    return (
      <div>
        {content}
      </div>
    )
  }
}
