import React, { PureComponent } from 'react'
import Navbar from './Navbar';
import database from '../data';
import loading from '../images/loading.gif'
import Answer from './Answer';
import Topics from './Topics'
import DraftEditor from '../DraftEditor'


export default class Full_Answer extends PureComponent {
        constructor(){
            super();
            this.state = {
                data:[],
                isLoading:true,
                title:'',
                openeditor:false,
                question_id:'',
                editorContent:''
            }
        }
    closeEditor = ()=>{
        this.setState({openeditor:false})
    }
    writeAnswer = ()=>{
        this.setState({openeditor:true})
    }
    updateEditorContent = (editorState)=>{
        this.setState({editorContent:editorState})
    }
    addNewAnswer = ()=>{
        let new_answer = {
            id:database.answers.length+1,
            answered_by:'user1',
            answer:this.state.editorContent,
            upvotes:0,
            downvotes:0,
            accepted:false,
            question_id:this.state.question_id
        }
        console.log(new_answer)
        this.setState((prev)=>{
            return {
                data:[...prev.data, new_answer],
                openeditor:false
            }
        })

    }
    componentDidMount(){
        this.setState({isLoading:true});
        let id = parseInt(this.props.match.params.id);
        let data = database.answers.filter((answer)=>{
                 return parseInt(answer.question_id)===id;
        })
        let title = database.questions.filter((question)=>{
                    return parseInt(question.id)===id;
            })
        // console.log(data)

        setTimeout(()=>{
            this.setState({
                    data:[... data],
                    isLoading:false,
                    title:title[0].title,
                    question_id:id
            })
        },1000);
    }
  render() {
    let content = <img src={loading} alt="loading" className="img-responsive" />;
    let heading='';
    let writingSection='';
    if(!this.state.isLoading){
        heading = <p className="title source_code m-2">{this.state.title}</p>;
        writingSection = <div className="m-3"><button onClick={this.writeAnswer} style={{display:this.state.openeditor?'none':'block'}} className="btn btn-primary">Write Your Answer‚úçÔ∏è</button></div>;
        
               // console.log(typeof this.state.data)
                content = Object.values(this.state.data).map((answer,index)=>{
                // console.table({index,answer})
                return <Answer data={answer} key={index}/>;
            })
    }

    return (
        <div>
        <Navbar />
        <header>
        <div className="container m-3">
          <div className="row">
           <div className="col-3">
               <Topics />
           </div>
           <div className="col-8">
               {heading}
               {content}
               {writingSection}
               {this.state.openeditor?<div className="container-m-5 card editor"><span className="btn text-right" onClick={this.closeEditor}>üñæ</span><DraftEditor  update={this.updateEditorContent} submit={this.addNewAnswer}/></div>:null}
           </div>
          </div>
        </div>
        </header>
      </div>
    )
  }
}
