import React, { PureComponent } from 'react';
import {HashRouter as Router,Route} from 'react-router-dom'
import Home from './components/Home'
import Profile from './components/Profile';
import NotFound from './components/404';
import Questions from './components/Questions';
import Full_Answer from './components/Full_Answer';
import Notifications from './components/Notifications';
import Ask from './components/Ask';
import ViewTopic from './components/ViewTopic';

class App extends PureComponent {
  render() {
    return (
      <Router>
         <Route path="/" exact component={Home} />
         <Route path="/home"  component={Home} />
         <Route path="/profile/:username"  component={Profile} />
         <Route path="/answers"  component={Questions} />
         <Route path="/full_answer/:id"  component={Full_Answer} />
         <Route path="/notifications"  component={Notifications} />
         <Route path="/ask"  component={Ask} />
         <Route path="/topics/:id"  component={ViewTopic} />
         <Route path="/404"  component={NotFound} />
      </Router>
    );
  }
}

export default App;
