import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Question extends Component {
  render() {
      let {title,asked_by,answer_ids,id} = this.props.data;
      // console.table({title,asked_by,answer_ids});
    return (
      <div>
        <div className="row source_code">
          <div className="col-12 card m-2 container-p-5">
          <p className="title"><Link to={"/full_answer/"+id} >{title}</Link></p>
           <span className="other_info">{answer_ids.length===0?<b>No answered yet</b>:<b>{answer_ids.length} Answer</b>}</span>
           <span className="other_info">asked by <span><Link to={"/profile/"+asked_by} className="blue">{asked_by}</Link></span></span>
          </div>
        </div>
      </div>
    )
  }
}
