import React, { PureComponent } from 'react'
import Navbar from './Navbar';
import database from '../data'
import loading from '../images/loading.gif'
import Topics from './Topics'
import Question from './Question'


export default class ViewTopic extends PureComponent {
   state = {
       data:[],
       isLoading:true,
       title:'',
       id:parseInt(this.props.match.params.id)
   }
   componentWillReceiveProps(nextProps){
            
             if(nextProps.match.params.id!=this.props.match.params.id){
                this.setState({id:parseInt(nextProps.match.params.id)});
             }
            //  console.log(this.state.id)
   }
   componentDidUpdate(prevProps, prevState){
          if(prevState.id!=this.state.id){
                 this.updateData();
          }
   }

   componentDidMount(){
       this.updateData()
   }
   updateData = ()=>{
    if(this.state.id>=database.topics.length){
        this.setState({data:[],isLoading:false,title:'Topic Not Found'})
    }
    else{
      console.log(this.state.id)
     let question_ids = database.topics[this.state.id].question_ids;
     
     let data = database.questions.filter((question)=>{
               question_ids.forEach(element => {
                   if (question.id===element){
                       return true;
                   }
               });
               return false;
     })
     this.setState({
         data:data,
         isLoading:false,
         title:database.topics[this.state.id].name
     })
    }
   }
  render() {
      let content = <img src={loading} alt="loading" className="img-responsive" />;
      if(!this.state.isLoading){
         content = this.state.data.map((question)=>{
               return <Question data={question} key={question.id}/>
         })
      }

    return (
      <div>
        <Navbar />
        <div className="container  container-m-5">
           <div className="row">
                <div className="col-3">
                        <Topics />
                </div>
               <div className="col-9">
                      <p className="title source_code p-3  card">{this.state.title}</p>
               </div>
           </div>
           <div className="row">
               <div className="col-3">
                   
               </div>
               <div className="col-7">
                  {content}
               </div>
           </div>
        </div>
      </div>
    )
  }
}
