import React, { PureComponent } from 'react'
import Topic from './Topic'
import database from '../data'
export default class Topics extends PureComponent {
    constructor(){
        super()
        this.state = {
            isLoading:false,
            data:database.topics
        }
    }
  render() {
      let topics = <p className="text-center">Loading...</p>;
      if(!this.state.isLoading){
          topics = this.state.data.map((topic)=>{
              return <Topic data={topic} key={topic.id}/>;
          }) 
      }
    return (
      <div>
        {topics}
      </div>
    )
  }
}
