import React, { Component } from 'react'
import Navbar from './Navbar'
import Topics from './Topics'
import Posts from './Posts'
export default class Home extends Component {
  render() {
    return (
      <div>
        <Navbar />
        <header>
        <div className="container m-3">
          <div className="row">
           <div className="col-3">
               <Topics />
           </div>
           <div className="col-7">
             <Posts />
           </div>
          </div>
        </div>
        </header>
      </div>
    )
  }
}
