import React, { Component } from 'react'
import {Link } from 'react-router-dom'
export default class Topic extends Component {
  render() {
    let url = "/topics/" + this.props.data.id;
    return (
      <div>
        <div className="row topic source_code">
          <div className="col-12">
            <Link className="link_black" to={url}>
                <img src={this.props.data.topic_img} alt="img" className="topic_img"/>
                <span className="px-1">{this.props.data.name}</span>
            </Link>
          </div>
        </div>
      </div>
    )
  }
}
